
import './App.css';
import {
  BrowserRouter as Router,
  Switch,
  Route,
} from "react-router-dom";

import 'bootstrap/dist/css/bootstrap.min.css';
import Login from "./pages/Login";


import Dashboard from './pages/Dashboard';

import Owner from './pages/Owner';
import Tenant from './pages/Tenant';

import PropertyListing from './pages/PropertyListing';
import PropertyView from './pages/PropertyView';
import MyPropertyView from './pages/MyPropertyView';
import PostProperty from './pages/PostProperty';
import MyProperties from './pages/MyProperties';
import {useDispatch, useSelector} from "react-redux";
import {useEffect} from "react";
import {getUser} from "./services/storage";
import {setAuthUser} from "./slices/Auth.slice";
function App() {


  const dispatch = useDispatch();
  const {user} = useSelector(state=>state?.auth);

  useEffect(()=>{
    if(!user)
    {
       const authUser = getUser();
       console.log(authUser);
       if(authUser)
       {
          dispatch(setAuthUser(authUser));
       }
    }
  },[user])
  if(!user || !Object.values(user)?.length)
  {
    return (<Login/>)
  }
  return (
      <Router>
        <div>
          <Switch>
            <Route exact path="/">
              <Dashboard />
            </Route>
              <Route exact path="/owner">
                  <Owner />
              </Route>
              <Route exact path="/tenant">
                  <Tenant />
              </Route>
            <Route exact path="/tenant/property-listing">
              <PropertyListing />
            </Route>
            <Route exact path="/tenant/property-view">
              <PropertyView />
            </Route>
              <Route exact path="/owner/my-property-view">
              <MyPropertyView />
            </Route>
            <Route exact path="/owner/post-new-property">
              <PostProperty />
            </Route>
            <Route exact path="/owner/my-property-list">
              <MyProperties />
            </Route>
          </Switch>
        </div>
      </Router>
  );
}

export default App;
