import React, {useState} from 'react';
import './style.css';
import axios from "axios";
import {setAuthUser} from "../../slices/Auth.slice";
import {useDispatch} from "react-redux";
import {setToken, setUser} from "../../services/storage";
import {USER_LOGIN} from '../../services/api'

function Index() {

    const dispatch = useDispatch();

    const [emailId,setEmailId] = useState(null);
    const [publicKey,setPublicKey] = useState(null);
    const [userName,setUserName]   = useState(null);

    const login = async (event)=> {
        event.preventDefault();
        if(!userName)
        {
            alert("please enter your username");
            return false;
        }
        if(!emailId)
        {
            alert("please enter your email id");
            return false;
        }
        if(!publicKey)
        {
            alert("please enter public key");
            return false;
        }


        const params = {emailId,publicKey,userName};
        
        try
        {
            const response = await axios.get(USER_LOGIN, {params});
            if(response.status===200)
            {
                const result = response.data;
                if(result.status==="SUCCESS")
                {
                    dispatch(setAuthUser(result.data[0]));
                    setUser(result.data[0]);
                    setToken(result.data[0]?.publicKey);
                }
                else
                {
                    alert(result.message);
                }
            }
            else
            {
                alert(response.message);
            }
        }
        catch (e)
        {
            alert(e.toLocaleString());
        }


    }
    return (
        <div className="container">
            <div className="row justify-content-center my-5">
                <div className="col-md-5">
                    <div className="auth-wrapper">
                        <div className="auth-inner">
                            <form onSubmit={login} autoComplete="off">
                                <h3>Welcome Back !</h3>
                                <div className="form-group">
                                    <span className="my-1">
                                    <label htmlFor="username">Enter Username</label>
                                    </span>

                                    <input type="text" name="username" id="username" className="form-control"
                                           placeholder="" onChange={e=>setUserName(e.target.value)} autoComplete="false"/>
                                </div>
                                <div className="form-group">
                                    <span className="my-1">
                                    <label htmlFor="emailId">Enter Email</label>
                                    </span>

                                    <input type="text" name="emailId" id="emailId" className="form-control"
                                           placeholder="" onChange={e=>setEmailId(e.target.value)} autoComplete="false"/>
                                </div>

                                <div className="form-group">
                                    <span className="my-1">
                                    <label htmlFor="publicKey">Public Key</label>
                                    </span>
                                    <input type="text" name="publicKey" id="publicKey" onChange={e=>setPublicKey(e.target.value)} className="form-control"
                                           placeholder=""/>
                                </div>
                                <div className="my-2">
                                    <button  className="btn btn-primary btn-block">Login</button>
                                </div>

                            </form>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}


export default Index;
