import React from 'react';
import {Link} from "react-router-dom";
import {useDispatch} from "react-redux";
import {setAuthUser} from "../../slices/Auth.slice";
import {removeToken, removeUser} from "../../services/storage";

function Index () {

    const dispatch = useDispatch();

    const logout = ()=>{
        dispatch(setAuthUser({}));
        removeToken();
        removeUser();
    }
    return (
        <nav className="navbar navbar-expand-lg navbar-light mx-0">
            <div className="container">
                <Link className="navbar-brand" to={"/"}>Trusted Properties</Link>
                <div className="collapse navbar-collapse" id="navbarToggle">
                    <ul className="navbar-nav ml-auto">
                        <li className="nav-item">
                            <Link className="nav-link" to={"/about"}>About</Link>
                        </li>
                        <li className="nav-item">
                            <Link className="nav-link" to={"/contact"}>Contact</Link>
                        </li>
                    </ul>
                    <ul className="navbar-nav mr-auto">
                        <li className="nav-item">
                            <button onClick={()=>logout()}  className="btn nav-link btn-danger text-white fw-bold">Log Out</button>
                        </li>

                    </ul>
                </div>
            </div>
        </nav>
    );
}

export default Index;
