import {reactLocalStorage} from 'reactjs-localstorage';

export const setToken =  token => {
    return  reactLocalStorage.set('token', token);
};

export const setUser =  user => {
    return  reactLocalStorage.setObject('user', user);
};
export const removeToken =  () => {
    return reactLocalStorage.remove('token');
};

export const removeUser =  () => {
    return reactLocalStorage.remove('user');
};

export const getToken =  () => {
    return reactLocalStorage.get('token','',true);
};

export const getUser =  () => {
    return reactLocalStorage.getObject('user',{},true);
};


