import React, {useEffect, useState} from 'react';
import './my-property.css';

import Navbar from '../../components/NavBar';
import HomeImage from '../../assets/images/home.png';
import {Link} from "react-router-dom";
import {useDispatch, useSelector} from "react-redux";
import {fetchProperty} from "../../slices/Property.slice";
import {getUser} from "../../services/storage";

const Index = () => {

    const dispatch = useDispatch();
    const [myProperties,setMyProperties] = useState([]);
    const {properties} = useSelector(state=>state?.properties);
    const user = getUser();

    useEffect(()=>{

        dispatch(fetchProperty());

    },[])

    useEffect(()=>{

        if(properties?.length>0)
        {
            let list = properties.filter(el=>{
                return el?.userId===user?.userId
            });

            setMyProperties(list);
        }

    },[properties])

    const [pincode, setPincode] = useState(null);

    const search = term => {
        dispatch(fetchProperty({pincode:term}));
    }
    const reset = ()=>{
        dispatch(fetchProperty());
    }
    return (
        <div>
            <Navbar/>
            <div className="container my-5">

                <div className="row my-2">
                    <div className="col-md-12">
                        <nav className="text-white" aria-label="breadcrumb">
                            <ol className="breadcrumb text-white fw-bold">
                                <li className="breadcrumb-item"><Link className="text-white fw-bold" to="/">Home</Link></li>
                                <li className="breadcrumb-item"><Link className="text-white fw-bold" to="/owner">Owner</Link></li>
                                <li className="breadcrumb-item active text-white fw-bold" aria-current="page">My Property List</li>
                            </ol>
                        </nav>
                    </div>
                </div>

                <h4 className={"text-white fw-bold"}> My Property List</h4>
                <div className="row">
                    <div className="col-md-4">
                        <div className="input-group mb-3">
                            <input type="text"
                                   onChange={event => setPincode(event.target.value)}
                                   className="form-control" placeholder="Enter Pincode"
                                   aria-label="Recipient's username" aria-describedby="button-addon2"/>
                            <button onClick={()=>search(pincode)}  className="btn btn-success" type="button" id="button-addon2">Search
                            </button>
                            <button onClick={()=>reset()}  className="btn btn-warning" type="button" id="button-addon2">Reset
                            </button>
                        </div>
                    </div>

                </div>
                <div className={"row"}>

                    {
                        myProperties?.length > 0 ? myProperties.map(item => {

                            return (
                                <div key={Math.random().toString()} className={"col col-md-4"}>
                                    <div className={"card my-2"}>
                                        <div className={"card-body row"}>
                                            <div className={"col-md-4"}>
                                                <img className="property-image" src={HomeImage}/>
                                            </div>
                                            <div className={"col-md-8"}>
                                                <h6 className={"card-title"}>{item?.propertyName}</h6>
                                                <div>
                                                    <table className="table table-striped table-borderless">
                                                        <tbody>
                                                        <tr>
                                                            <th>House No.</th>
                                                            <td>{item?.houseNo}</td>
                                                        </tr>
                                                        <tr>
                                                            <th>Flat No.</th>
                                                            <td>{item?.flatNo}</td>
                                                        </tr>
                                                        <tr>
                                                            <th>Rent Per Month</th>
                                                            <td>{item?.rentPerMonth}</td>
                                                        </tr>
                                                        <tr>
                                                            <th>Security Deposit</th>
                                                            <td>{item?.securityDepositAmount}</td>
                                                        </tr>
                                                        <tr className={item?.availability ? 'bg-danger':'bg-success'}>
                                                            <th className="text-white">Availability</th>
                                                            <td className="text-white">{item?.availability ? 'Not Rented':'Rented'}</td>
                                                        </tr>
                                                        </tbody>
                                                    </table>
                                                </div>
                                            </div>
                                        </div>
                                        <div className="card-footer bg-primary text-center">
                                            <Link to={{pathname: `/owner/my-property-view`,state: item }} className={"text-white fw-bold"}>View</Link>
                                        </div>
                                    </div>

                                </div>
                            )
                        }) : <div>
                            <h6 className="text-white fw-bold">No data found</h6>
                        </div>
                    }
                </div>
            </div>
        </div>
    );
}

export default Index;
